// amplify/data/knowledgeBaseLambdaSimple.ts
import type { Schema } from './resource';

export const handler: Schema["queryKnowledgeBase"]["functionHandler"] = async (event) => {
  console.log('Simple Knowledge Base Lambda invoked');
  
  const { query } = event.arguments;
  
  if (!query) {
    return {
      response: null,
      sourceDocuments: [],
      error: 'Query is required'
    };
  }

  // 一時的にモックレスポンスを返す
  console.log('Mock response for query:', query);
  
  return {
    response: `これは"${query}"に対するテスト応答です。実際のKnowledge Base接続は後で実装されます。`,
    sourceDocuments: ['test-document-1.pdf', 'test-document-2.pdf'],
    error: null
  };
};